import { addInfo, getTitleList } from '@/api/api'
import { Toast } from 'vant'
export default {
  name: 'Home',
  data() {
    return {
      name: '',
      fromBy: '',
      location: '',
      columns: ['官方网站', '官方微信', '朋友介绍', '到校了解', '地推宣传', '其他'],
      schools: ['勤昆路分校', '古城路分校', '庆丰路分校', '张浦分校', '宝轮分校'],
      showPicker: false,
      titleList: [],
      listLength: '',
      radioData: []
    }
  },
  mounted() {
    this._getTitleList()
  },
  methods: {
    onConfirm(value) {
      this.value = value
      this.showPicker = false
    },
    _getTitleList() {
      getTitleList().then(res => {
        console.log(res.data)
        const arr = []
        res.data.map((item, index) => {
          arr.push({
            title: item.title,
            title_no: item.id,
            result: '',
            type: item.type
          })
        })
        this.radioData = arr
        this.listLength = res.data.length
      })
    },
    postInfo: function() {
      console.log(this.radioData)
      // 校验必填项
      const isExit = this.radioData.some((item, index) => {
        console.log(item, index)
        return item.result === ''
      })
      console.log(isExit)
      if (this.name === '') {
        Toast.fail('请填写教练名称')
        return false
      }
      if (this.fromBy === '') {
        Toast.fail('请选择报名渠道')
        return false
      }
      if (this.location === '') {
        Toast.fail('请选择驾校校区')
        return false
      }
      if (isExit) {
        Toast.fail('您还有问题未回答')
        return false
      }
      const data = {
        name: this.name,
        from_by: this.fromBy,
        location: this.location,
        result: this.radioData,
        openid: sessionStorage.getItem('openid')
      }
      console.log(data)
      addInfo(data).then(res => {
        console.log(res)
        Toast.success('res')
      })
    },
    handleRadio(e) {
