import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import { login } from '@/api/api'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  // mode: 'history',
  mode:'hash',
  base: process.env.BASE_URL,
  routes
})
// http://www.saberc8.cn/dist/?code=031qKYZv3lOoUU2TrH1w3rphKc3qKYZe&state=STATE
function delCodeandstate(to) {
  // 函数作用：去除url中的code和state
  let path = ''
  for (const i in to.query) {
    if (i !== 'code' && i !== 'state') {
      path = path + '&' + i + '=' + to.query[i]
    }
  }
  path = path === '' ? '' : path.substring(1, path.length)
  path = path === '' ? '' : '/?' + path
  return path
}

router.beforeEach((to, from, next) => {
  console.log(from, to)
  let fullPath = to.fullPath
  console.log(fullPath, 'fillpath')
  if (to.fullPath.includes('code')) {
    //  判断url中是否有code，踩坑1-页面反复跳转
    fullPath = delCodeandstate(to)
  }
  const redirect_uri = encodeURIComponent(window.location.href)
  const appid = 'wx43079171e0a73679' //  redirect_uri，授权登陆后的回调地址，需要进行encodeURIComponent处理
  const openid = sessionStorage.getItem('openid') // noLoginArr，白名单，不需要授权登陆的页面
  console.log(openid, 'openid')
  function splitUrl(url=window.location.href){
    url = window.location.href
    if(url.indexOf('?')>-1){
      console.log(url,'??',window.location.href,'??')
      let query = url.split('?')[1]
      let obj = {}
      if(query.indexOf('&')>-1){
        query = query.split('&')
        console.log(query,'??!!!')
        query.map(item=>{
          if(item.indexOf('=')>-1){
             let [key,value] = item.split('=')
             obj[key] = value
          }
        })
      }else{
        if(query.indexOf('=')>-1){
          let [key,value] = query.split('=')
             obj[key] = value
        }
      }
      console.log(obj,'??')
      return Object.keys(obj).length>0?obj:null
    }
  }
  const urlObj = splitUrl()
  const code = urlObj&&urlObj.code
  const state = urlObj&&urlObj.state
  console.log(code, state)
  if (openid && openid !== 'undefined' && openid !== null) {
    // 白名单内不做登录判断，直接next
    next()
  } else {
    if (code) {
      // 登陆之后获取到code，传到后台登录
      const params = {
        code: code
      }
      console.log(params, 'params')
      login(params).then(res => {
        console.log(res, 'login')
        if (res.data.openid !== '') {
          console.log(1111,res.data.openid)
          sessionStorage.setItem('openid', res.data.openid)
          next()
        } else {
          console.log('请求失败')
          window.location.href =
          'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' +
          appid +
          '&redirect_uri=' +
          redirect_uri +
          '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
        }
      })
    } else {
      console.log(code,'???')
      window.location.href =
        'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' +
        appid +
        '&redirect_uri=' +
        redirect_uri +
        '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
    }
  }
})
// window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx43079171e0a73679&redirect_uri=http%3A%2F%2Fwww.saberc8.cn%2Fdist%2Findex.html&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`
export default router
