import request from '@/utils/http'

export function addInfo(data) {
  return request({
    url: '/driving/commit',
    method: 'post',
    data
  })
}

export function getTitleList(params) {
  return request({
    url: '/driving/titleList',
    method: 'get',
    params
  })
}

export function login(params) {
  return request({
    url: '/driving/login',
    method: 'get',
    params
  })
}
